import React from "react";
import { MDBCard } from "mdbreact";

export default function FoliaCard(props) {
  const imgurl = props.dados.images[0] ? props.dados.images[0].src : "";
  return (
    <MDBCard
      className="card-image"
      style={{
        backgroundImage: `url('${imgurl}')`
      }}
    >
      <div className="text-white text-left d-flex align-items-bottom py-5 px-5">
        {props.dados.name}
      </div>
    </MDBCard>
  );
}
