import React, { useState } from "react";
import ItemsCarousel from "react-items-carousel";
import FoliaCard from "./FoliaCard";
import { MDBIcon } from "mdbreact";

export default function FoliaCarousel(props) {
  const [activeItemIndex, setActiveItemIndex] = useState(0);
  const chevronWidth = 0;
  const secaoTituloSt = { color: "#4A4A4A!important" };
  return (
    <section className="text-center my-3 w-100">
      <h2
        className="h1-responsive font-weight-bold text-left my-2"
        style={secaoTituloSt}
      >
        {props.titulo}
      </h2>
      <div style={{ padding: `0 ${chevronWidth}px` }}>
        <ItemsCarousel
          requestToChangeActive={setActiveItemIndex}
          activeItemIndex={activeItemIndex}
          numberOfCards={1}
          gutter={20}
          leftChevron={<MDBIcon icon="chevron-left" size="lg" />}
          rightChevron={<MDBIcon icon="chevron-right" size="lg" />}
          outsideChevron={false}
          alwaysShowChevrons={true}
          chevronWidth={chevronWidth}
        >
          {props.dados.map(item => (
            <FoliaCard key={item.id} dados={item} />
          ))}
        </ItemsCarousel>
      </div>
    </section>
  );
}
